:root {
  --header-background: url("./header-background.jpg");
  --primary-color: #5BD9FB;
}

.header {
  height: 100vh;
  background-image: var(--header-background);
  background-size: cover;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

.header__title {
  text-transform: uppercase;
  z-index: 1;
  font-size: 72px;
  white-space: nowrap;
  text-shadow: 1px 1px 10px #000000;
  color: #ffffff;
  text-transform: uppercase;
  margin: 0;
}

.header__title-secondary-wrapper {
  overflow: hidden;
  z-index: 1;
  margin-top: 10px;
}

.header__title-secondary {
  color: var(--primary-color);
  font-size: 48px;
  text-transform: uppercase;
  margin: 0;
}

.header__pieces {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -webkit-perspective: 400px;
          perspective: 400px;
}

 .header__piece-wrap {
   position: absolute;
   width: 100%;
   height: 100%;
 }

.header__piece {
  background-image: var(--header-background);
  background-size: cover;
  width: 100%;
  height: 100%;
}

.header__piece__shadow {
  position: absolute;
  -webkit-box-shadow: 0 7px 30px 1px rgba(0, 0, 0, 0.45);
          box-shadow: 0 7px 30px 1px rgba(0, 0, 0, 0.45);
}


